import React from "react";
import { useTranslation } from "react-i18next";

import styled from "styled-components"; //eslint-disable-line
import tw from "twin.macro";

import { ReactComponent as SvgDecoratorBlob1 } from "assets/images/svg-decorator-blob-9.svg";
import { ContainerWithStandarPadding as Container } from "layouts/Layouts.js";

const GrayBackgroundContainer = tw.div`px-4 py-12 md:py-16 bg-gray-100 rounded-3xl relative`;
const Row = tw.div`px-8 max-w-screen-lg mx-auto flex items-center relative z-10 flex-col lg:flex-row text-center lg:text-left`;

const TextContainer = tw.div`lg:w-4/5 max-w-xl`;
const Text = tw.h5`text-gray-900 text-2xl md:text-3xl font-semibold md:leading-relaxed`;

const LinksContainer = tw.div`lg:w-1/2 max-w-lg flex justify-center lg:justify-end mt-6 lg:mt-0 flex-col sm:flex-row`;

const Link = tw.a`
    w-full sm:w-auto text-sm sm:text-base px-6 py-3 sm:px-8 sm:py-4 
    lg:px-10 lg:py-5 mt-4 first:mt-0 sm:mt-0 sm:mr-8 sm:last:mr-0 rounded-full 
    font-bold border border-transparent
    tracking-wide transition duration-300 focus:outline-none focus:ring
`;
const PrimaryLink = tw(
  Link
)`bg-gray-900 text-gray-100 shadow-md hocus:shadow-xl hocus:text-gray-100`;

const DecoratorBlobContainer = tw.div`absolute inset-0 overflow-hidden rounded-lg`;
const DecoratorBlob1 = tw(
  SvgDecoratorBlob1
)`absolute bottom-0 left-0 w-80 h-80 transform -translate-x-20 translate-y-32 text-gray-300 opacity-70`;
const DecoratorBlob2 = tw(
  SvgDecoratorBlob1
)`absolute top-0 right-0 w-80 h-80 transform  translate-x-20 -translate-y-64 text-gray-300 opacity-70`;
export default ({ pushDownFooter = false }) => {
  const { t } = useTranslation();
  const downloadLinkUrl = "#";

  return (
    <Container>
      <GrayBackgroundContainer>
        <Row>
          <TextContainer>
            <Text>{t("JoinUs.discription")}</Text>
          </TextContainer>
          <LinksContainer>
            <PrimaryLink href={downloadLinkUrl}>
              {t("JoinUs.download")}
            </PrimaryLink>
          </LinksContainer>
        </Row>
        <DecoratorBlobContainer>
          {/* <DecoratorBlob1 /> */}
          <DecoratorBlob2 />
        </DecoratorBlobContainer>
      </GrayBackgroundContainer>
    </Container>
  );
};
