import React, { useState } from "react";
import { useTranslation } from "react-i18next";

import tw from "twin.macro";
import styled from "styled-components";
import { motion } from "framer-motion";
import Slider from "react-slick";

import { SectionHeading } from "components/misc/Headings.js";
import { PrimaryLink as PrimaryLinkBase } from "components/misc/Links.js";
import { ReactComponent as ArrowRightIcon } from "assets/images/arrow-right-icon.svg";
import { PrimaryButton as PrimaryButtonBase } from "components/misc/Buttons.js";
import { ReactComponent as SvgDecoratorBlob1 } from "assets/images/svg-decorator-blob-7.svg";
import { ReactComponent as SvgDecoratorBlob2 } from "assets/images/svg-decorator-blob-8.svg";
import { ReactComponent as ChevronLeftIcon } from "feather-icons/dist/icons/chevron-left.svg";
import { ReactComponent as ChevronRightIcon } from "feather-icons/dist/icons/chevron-right.svg";

import { ContainerWithStandarPadding as Container } from "layouts/Layouts.js";

const Heading = tw(SectionHeading)`text-left lg:text-3xl`;
const CardSlider = styled(Slider)`
  ${tw`mt-8 overflow-hidden`}
  .slick-track {
    ${tw`flex`}
  }
  .slick-slide {
    ${tw`h-auto flex justify-center mb-1`}
  }
`;
const HeadingWithControl = tw.div`flex flex-col items-center sm:items-stretch sm:flex-row justify-between `;
const Controls = tw.div`flex items-center`;
const ControlButton = styled(PrimaryButtonBase)`
  ${tw`mt-4 sm:mt-0 first:ml-0 ml-6 rounded-full p-2`}
  svg {
    ${tw`w-6 h-6`}
  }
`;
const PrevButton = tw(ControlButton)``;
const NextButton = tw(ControlButton)``;

const PopularPostsContainer = tw.div`lg:w-full`;
const Post = tw(
  motion.a
)`relative w-full cursor-pointer mb-12 px-4 focus:outline-none`;
const Image = styled(motion.div)((props) => [
  `background-image: url("${props.imageSrc}");`,
  tw`h-56 bg-auto bg-cover bg-center rounded`,
]);
const Title = tw.h5`sm:h-20 mt-6  text-lg overflow-hidden font-semibold transition duration-300 group-hover:text-gray-500`;
const Description = tw.p`mt-2 font-light  text-gray-500 leading-loose text-sm`;
const AuthorInfo = tw.div`mt-6 flex items-center`;
const AuthorImage = tw.img`w-12 h-12 rounded-full`;
const AuthorNameAndProfession = tw.div`ml-4`;
const AuthorName = tw.h6`font-semibold text-lg mb-0`;
const AuthorProfile = tw.p`text-gray-500 text-sm mb-0`;

const LinkContainer = tw.div`text-center mb-8 lg:mb-16`;
const PrimaryLink = styled(PrimaryLinkBase)`
  ${tw`inline-flex justify-center items-center text-lg`}
  svg {
    ${tw`ml-2 w-5 h-5`}
  }
`;

const DecoratorBlob1 = styled(SvgDecoratorBlob1)`
  ${tw`pointer-events-none -z-20 absolute left-0 top-0 h-56 w-56 opacity-25 transform -translate-x-2/3 -translate-y-12 text-blue-400`}
`;
const DecoratorBlob2 = styled(SvgDecoratorBlob2)`
  ${tw`pointer-events-none -z-20 absolute right-0 bottom-0 h-64 w-64 opacity-25 transform translate-x-2/3 text-yellow-500`}
`;

export default () => {
  const { t } = useTranslation();

  const [sliderRef, setSliderRef] = useState(null);
  const sliderSettings = {
    arrows: false,
    slidesToShow: 3,
    responsive: [
      {
        breakpoint: 1080,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 640,
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };

  // This setting is for animating the post background image on hover
  const postBackgroundSizeAnimation = {
    rest: {
      backgroundSize: "100%",
    },
    hover: {
      backgroundSize: "110%",
    },
  };

  const popularPosts = [
    {
      postImageSrc:
        "https://images.unsplash.com/photo-1534940519139-f860fb3c6e38?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1347&q=80",
      authorImageSrc: "https://i.imgur.com/dTpRit4.jpg",
      title:
        " Identifying Tomato Diseases and Pests Using Leave Images and Convolutional Neural Networks",
      description:
        "Lorem ipsum dolor sit amet, consecteturious adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua now ele.",
      authorName: "Barry Cheng",
      authorProfile: "Master's student",
      url: "#",
    },
    {
      postImageSrc:
        "https://images.unsplash.com/photo-1515445946706-a8024608d459?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80",
      authorImageSrc: "https://i.imgur.com/ZzFffJV.jpg",
      title:
        "Developing Deep Learning Cloud Services for Wood and Bamboo Identification ",
      description:
        "Lorem ipsum dolor sit amet, consecteturious adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua now ele.",
      authorName: "Kuan-Ting Yeh",
      authorProfile: "Master's student",
      url: "/projects/wood",
    },
    {
      postImageSrc:
        "https://images.unsplash.com/photo-1471119115842-96070c40566d?ixlib=rb-1.2.1&auto=format&fit=crop&w=1352&q=80",
      authorImageSrc: "https://i.imgur.com/0PI9nAp.jpg?1",
      title: "System Design of a Chicken Monitoring Robot",
      description:
        "Lorem ipsum dolor sit amet, consecteturious adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua now ele.",
      authorName: "Yi-Hsin Yeh",
      authorProfile: "Master's student",
      url: "#",
    },
    {
      postImageSrc: "https://i.imgur.com/3YIpQJA.jpg",
      authorImageSrc: "https://i.imgur.com/BhMWqHA.jpg?1",
      title:
        "Automatical Localization and Tracking of Piglets Using Convolutional Neural Networks",
      description:
        "Lorem ipsum dolor sit amet, consecteturious adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua now ele.",
      authorName: "Jimmy Ho",
      authorProfile: "Master's student",
      url: "#",
    },
    {
      postImageSrc:
        "https://images.unsplash.com/photo-1588159482223-dbecdae250a7?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80",
      authorImageSrc:
        "https://scontent-tpe1-1.xx.fbcdn.net/v/t1.0-9/36513712_2084912294855800_6049412353529741312_n.jpg?_nc_cat=100&ccb=2&_nc_sid=09cbfe&_nc_ohc=5DeDW3oznbAAX87tQsn&_nc_ht=scontent-tpe1-1.xx&oh=2aaf8f1bacc3d799d53175f9fc51548e&oe=5FD9CFAD",
      title: "Segmenting fish body using deep convolutional neural network",
      description:
        "Lorem ipsum dolor sit amet, consecteturious adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua now ele.",
      authorName: "Hong-Yang Lin",
      authorProfile: "Master's student",
      url: "#",
    },
  ];

  return (
    <>
      <Container>
        <HeadingWithControl>
          <Heading>{t("OurProjects.sectionHeading")}</Heading>
          <Controls>
            <PrevButton onClick={sliderRef?.slickPrev}>
              <ChevronLeftIcon />
            </PrevButton>
            <NextButton onClick={sliderRef?.slickNext}>
              <ChevronRightIcon />
            </NextButton>
          </Controls>
        </HeadingWithControl>
      </Container>
      <Container tw="px-4! sm:px-12! xl:px-44!">
        <PopularPostsContainer>
          <CardSlider ref={setSliderRef} {...sliderSettings}>
            {popularPosts.map((post, index) => (
              <Post
                key={index}
                href={post.url}
                className="group"
                initial="rest"
                whileHover="hover"
                animate="rest"
              >
                <Image
                  transition={{ duration: 0.3 }}
                  variants={postBackgroundSizeAnimation}
                  imageSrc={post.postImageSrc}
                />
                <Title>{post.title}</Title>
                <Description>{post.description}</Description>
                <AuthorInfo>
                  <AuthorImage src={post.authorImageSrc} />
                  <AuthorNameAndProfession>
                    <AuthorName>{post.authorName}</AuthorName>
                    <AuthorProfile>{post.authorProfile}</AuthorProfile>
                  </AuthorNameAndProfession>
                </AuthorInfo>
              </Post>
            ))}
          </CardSlider>
        </PopularPostsContainer>
        <LinkContainer>
          <PrimaryLink>
            {t("OurProjects.viewAll")} <ArrowRightIcon />
          </PrimaryLink>
        </LinkContainer>

        <DecoratorBlob1 />
        {/* <DecoratorBlob2 /> */}
      </Container>
    </>
  );
};
